@import "https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Bree+Serif&family=EB+Garamond:ital,wght@0,500;1,800&display=swap";
body {
  background: #263038 0 0 / cover fixed;
}

#container {
  text-align: center;
  height: 350px;
  width: 1000px;
  background: rgba(255, 255, 255, .9);
  border-radius: 5px;
  margin: 3em auto;
  overflow: hidden;
  box-shadow: 0 15px 15px 1px gray;
}

.breed-details {
  text-align: left;
  height: 100%;
  float: left;
  width: 50%;
  padding: 30px;
  position: relative;
  overflow: hidden;
}

#container .breed-details h1 {
  color: #344055;
  margin: 0;
  font-family: Bebas Neue, cursive;
  font-size: 30px;
  display: inline-block;
  position: relative;
}

#container .breed-details h3 {
  color: #344055;
  margin: 0;
  font-family: Bree Serif, serif;
  display: inline-block;
  position: relative;
}

#container .breed-details h1:before {
  content: "";
  color: #fff;
  background: #ffe6e6;
  border-radius: 5px;
  margin: 0;
  padding: 5px;
  font-family: Bree Serif, serif;
  font-size: 14px;
  animation: chan-sh 6s infinite;
  display: inline-block;
  position: absolute;
  top: 0%;
  right: 0%;
  transform: translate(25px, -15px);
}

#container .breed-details > p {
  text-align: center;
  color: #7d7d7d;
  font-family: EB Garamond, serif;
  font-size: 18px;
}

.breed-image {
  height: 100%;
  width: 40%;
  float: right;
  transition: all .3s ease-out;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

#container img {
  width: 100%;
  height: 100%;
  background-size: contain;
}

.info {
  text-align: left;
  cursor: pointer;
  color: #fff;
  height: 100%;
  width: 100%;
  background: rgba(27, 26, 26, .9);
  font-family: Bree Serif, serif;
  font-size: 105%;
  line-height: 1.8;
  transition: all .3s ease-out;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.info h2 {
  text-align: center;
}

.breed-image:hover .info {
  transform: translateX(0);
}

.info ul li {
  transition: all .3s;
}

.info ul li:hover {
  transform: translateX(50px)scale(1.3);
}

.breed-image:hover img {
  transition: all .3s ease-out;
  transform: scale(1.2);
}

.select-breed {
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  display: none;
}

.breed-select {
  height: 40px;
  scroll-behavior: smooth;
  color: #344055;
  border: 0 solid #f87009;
  border-bottom-width: 2px;
  outline: none;
  font-family: Bree Serif, serif;
  box-shadow: 0 10px 15px 1px gray;
}

.select-info {
  width: 300px;
}

.inf {
  justify-content: center;
  align-items: center;
  margin: 30px;
  display: flex;
}

.error {
  color: #f87009;
  font-family: Bebas Neue, cursive;
  font-size: 30px;
}

.cat-info {
  justify-content: center;
  display: flex;
}

